@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
body {
  /* background-color: #354a55; */
  background-color: #334756;
  color: #ffffff;
  font-family: 'Roboto', sans-serif;
}

.Movie{position:relative}.Movie .poster{width:100%;box-shadow:0px -1px 11px 2px rgba(0,0,0,0.75)}.Movie .filter{-webkit-filter:brightness(30%);filter:brightness(30%);transition:ease-in-out 0.4s}.Movie .details{position:absolute;top:45%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);text-align:center}.Movie .details .star{width:20%;padding-bottom:10px}.Movie .details .rating{font-weight:bold;font-size:1.4rem}.Movie .details button{background-color:#FF4C29;border-color:#FF4C29;font-weight:500;font-size:0.9rem}.Movie .details button:focus{box-shadow:none}.Movie .details button:hover{background-color:#ce4225;border-color:#ce4225;transition:ease-in-out 0.3s}.Movie .hidden{visibility:hidden}.Movie a{text-decoration:none;color:white}.Movie .movie-title{padding-top:10px;font-weight:500;letter-spacing:1px;margin-bottom:0}.Movie .movie-year{color:#b9b9b9;font-weight:300}




.MovieDetails .poster img{width:100%;box-shadow:0px -1px 11px 2px rgba(0,0,0,0.75)}.MovieDetails h1{font-weight:700;letter-spacing:2px}.MovieDetails .year{font-weight:bold}.MovieDetails .genres{font-weight:700}.MovieDetails .overview{color:#a7a7a7;font-weight:300}.MovieDetails .trailer-button{background-color:#1a2f55;border-color:#1a2f55}.MovieDetails .trailer-link{color:white;text-decoration:none;font-weight:500}.MovieDetails .similar img{width:100%;box-shadow:0px -1px 11px 2px rgba(0,0,0,0.75)}.MovieDetails .similar .filter{-webkit-filter:brightness(30%);filter:brightness(30%);transition:ease-in-out 0.4s}

.Navbar .navbar{background-color:#082032 !important}.Navbar .navbar .navbar-brand{color:#FF4C29;font-weight:bold;letter-spacing:1px}.Navbar .navbar .nav-link{color:white;font-weight:500}.Navbar .navbar .nav-link:hover{color:#FF4C29}.Navbar .navbar .nav-link:focus{color:#FF4C29}.Navbar .navbar .active-page{color:#FF4C29}.Navbar .navbar form input{border-radius:5px 0px 0px 5px;border-color:#334756;background-color:#082032;color:white;padding-top:3px;padding-bottom:3px;font-weight:300}.Navbar .navbar form input:focus{box-shadow:none}.Navbar .navbar form button{border-width:1px 1px 1px 0px;border-color:#FF4C29;border-radius:0px 5px 5px 0px;background-color:#FF4C29}.Navbar .navbar form button:focus{box-shadow:none}.Navbar .navbar form button img{width:15px}


